  padding: 0.5rem 1.25rem;
  margin-right: 0.5rem;
  display: flex;
  gap: 0.5rem;
  align-items: center;
  background: #7549ff;
  border: 2px solid #7549ff;
  border-radius: 0.2rem;
  color: hsl(0, 0%, 100%);
  font-family: "Satoshi Variable", sans-serif;
  font-weight: 500 !important;
  text-decoration: none !important;
  text-align: center;
  flex-shrink: 0;
  cursor: pointer;
}
.button--primary.wide, .c4button.wide {
  padding: 0.75rem 1.75rem;
}
@media (prefers-reduced-motion: no-preference) {
  .button--primary, .c4button {
    transition: background 0.2s ease-in-out;
  }
}
.button--primary:focus, .c4button:focus {
  border-color: hsl(0, 0%, 100%);
  outline: none;
}
.button--primary:active, .c4button:active {
  border-color: hsl(251, 15.7%, 20%) !important;
  background: hsl(251, 15.7%, 20%) !important;
}
.button--primary:hover, .c4button:hover {
  background: #7f63ef;
  border-color: #7f63ef;
  color: hsl(0, 0%, 100%);
}
.button--primary[disabled], .c4button[disabled] {
  background: hsl(251, 15.7%, 20%) !important;
  border-color: hsl(251, 15.7%, 20%) !important;
  color: hsl(252, 10.9%, 36.1%) !important;
  cursor: not-allowed !important;
}

.button--secondary {
  background: transparent;
  border: 2px solid #7549ff;
  color: hsl(0, 0%, 100%);
}
.button--secondary:focus {
  background: #7549ff;
}
.button--secondary:hover {
  border-color: #7f63ef;
  background: #7549ff;
  color: hsl(0, 0%, 100%);
