color, 5%);\n\n$color__project: $color__project-color;\n$color__warden: $color__button-blurple;\n\n// Blended colors\n$color__blended-60: #4d22c7;\n\n$color__blended-80: #2c0e83;\n\n// Color Defaults\n// ------------------------------------------------\n\n// Background\n$color__background: $color__n-100;\n\n// Text\n$color__text: $color__n-10;\n$color__headline: $color__white;\n$color__subline: $color__n-10;\n\n// Forms\n$color__form-error: $color__red;\n\n// Buttons\n$color__button-danger: $color__red;\n$color__button-danger--hover: lighten($color__button-danger, 5%);\n\n// ðŸ” TODO add default font sizes and em spacing\n\n// Font Sizes\n// ------------------------------------------------\n\n$font-size__copy: 1.2rem;\n$font-size__copy__code: 0.9em; // I'm also using this for all code blocks\n$font-size__text: 1rem;\n$font-size__text__code: 0.9em;\n$font-size__small: 0.933rem;\n\n$headline-font-size__xxxs: 1rem; // Sublines\n$headline-font-size__xxs: 1.2rem; // H6\n$headline-font-size__xs: 1.3rem; // H5\n$headline-font-size__s: 1.3rem; // H4\n$headline-font-size__m: 1.6rem; // H3\n$headline-font-size__l: 1.8rem; // H2\n$headline-font-size__ll: 2.1rem; // H2\n$headline-font-size__xl: 2.533rem; // H1\n$headline-font-size__xxl: 3.2rem; // Hero headline\n$headline-font-size__xxxl: 3.2rem; // Landing page hero headline\n\n// Font Families\n// ------------------------------------------------\n\n$font__default: \"Satoshi Variable\", sans-serif;\n// ðŸ” TODO add DM sans as body copy font\n$font__code: \"PT Mono\", monospace;\n\n// Spacing\n// ------------------------------------------------\n\n$spacing__base: 0.5rem; // 8px\n\n$spacing__xxs: calc($spacing__base/ 4); // 2px\n$spacing__xs: calc($spacing__base/ 2); // 4px\n$spacing__s: $spacing__base; // 8px\n$spacing__m: calc($spacing__base * 2); // 16px\n$spacing__l: calc($spacing__base * 3); // 24px\n$spacing__xl: calc($spacing__base * 4); // 32px\n$spacing__xxl: calc($spacing__base * 8); // 64px\n$spacing__xxxl: calc($spacing__base * 10); // 80px\n\n// Breakpoints\n// ------------------------------------------------\n\n$breakpoint__xxs: 450px;\n$breakpoint__xs: 600px;\n$breakpoint__s: 750px;\n$breakpoint__m: 960px;\n$breakpoint__l: 1280px;\n$breakpoint__xl: 1920px;\n\n// Layout\n// ------------------------------------------------\n\n$layout__max-width: 1100px;\n$report__max-width: 900px;\n$contest__max-width: 1100px;\n$form__max-width: 900px;\n$newsletter-signup__max-width: 900px;\n\n// Shadows\n// ------------------------------------------------\n\n$shadow__base: 0 0 0.5rem 0 rgba(0, 0, 0, 0.5);\n$shadow__hover: 0 0 0.5rem 0 rgba(0, 0, 0, 0.75);\n\n// Border radius\n// ------------------------------------------------\n\n$border-radius__base: 0.2rem; // 4px\n$border-radius__l: calc($border-radius__base * 2); // 8px\n$border-radius__m: $border-radius__base; // 4px\n$border-radius__s: calc($border-radius__base/ 2); // 2px\n\n// Z-index\n// ------------------------------------------------\n\n$z-index__base: 1;\n$z-index__modal: 1000;\n\n// Media queries\n// ------------------------------------------------\n\n@mixin breakpoint($breakpoint) {\n  $breakpoints: (\n    \"xxs\": $breakpoint__xxs,\n    \"xs\": $breakpoint__xs,\n    \"s\": $breakpoint__s,\n    \"m\": $breakpoint__m,\n    \"l\": $breakpoint__l,\n    \"xl\": $breakpoint__xl,\n  );\n\n  @media (min-width: map-get($breakpoints, $breakpoint)) {\n    @content;\n  }\n}\n\n// Transitions\n// ------------------------------------------------\n\n$transition__base: 0.2s ease-in-out;\n\n// Animations\n// ------------------------------------------------\n\n// Example of using @media to wrap an animation mixin.\n@mixin accessible-animation {\n  @media (prefers-reduced-motion: no-preference) {\n    @content;\n  }\n}"],"sourceRoot":""}]);
